"use client";
import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Switch } from "@/components/ui/switch";
import theme from "@/styles/theme";

// Consumes: colors.primary, colors.accent, colors.background, colors.surface, colors.border, colors.textPrimary, colors.textSecondary, spacing.4, spacing.6, radii.card, shadows.medium

interface PlatformSettings {
  universityName: string;
  currentSession: string;
  currentSemester: string;
  gradingSystem: { [key: string]: number };
  cgpaScale: number;
  registrationOpen: boolean;
  resultPublicationOpen: boolean;
  maxCreditUnitsPerSemester: number;
}

/**
 * Platform Settings Dashboard
 * Handles all global platform configuration and semester controls.
 */
export default function PlatformSettingsPage() {
  const [settings, setSettings] = useState<PlatformSettings | null>(null);
  const [loading, setLoading] = useState(false);

  const router = useRouter();
  const token = typeof window !== "undefined" ? localStorage.getItem("token") : null;

  const defaultSettings: PlatformSettings = {
    universityName: "Adeyemi Federal University of Education",
    currentSession: "2025/2026",
    currentSemester: "First Semester",
    gradingSystem: { A: 70, B: 60, C: 50, D: 45, E: 40, F: 0 },
    cgpaScale: 5.0,
    registrationOpen: false,
    resultPublicationOpen: false,
    maxCreditUnitsPerSemester: 24,
  };

  // Fetch platform settings
  const getSettings = async () => {
    try {
      const res = await fetch("/api/settings", {
        headers: { Authorization: `Bearer ${token}` },
      });
      const data = await res.json();
      setSettings((data || defaultSettings) as PlatformSettings);
    } catch {
      console.error("Failed to load settings");
      setSettings(defaultSettings);
    }
  };

  useEffect(() => {
    if (!token) {
      router.push("/");
    } else {
      getSettings();
    }
  }, [token, router]);

  // Handle updates to settings
  const handleChange = (key: keyof PlatformSettings, value: any) => {
    setSettings((prev) => ({
      ...prev!,
      [key]: value,
    }));
  };

  // Save settings to backend
  const saveSettings = async () => {
    setLoading(true);
    try {
      const res = await fetch("/api/settings/update", {
        method: "PATCH",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify(settings),
      });
      const data = await res.json();
      setSettings(data as PlatformSettings);
      alert("Settings updated successfully");
    } catch (err) {
      console.error(err);
      alert("Error updating settings");
    } finally {
      setLoading(false);
    }
  };

  // Handle switch toggle
  const handleToggle = (key: keyof PlatformSettings) => {
    setSettings((prev) => ({
      ...prev!,
      [key]: !prev![key],
    }));
  };

  if (!settings) return <p className="text-center mt-10 text-textSecondary">Loading settings...</p>;

  return (
    <main className="min-h-screen bg-background p-4 md:p-6">
      <div className="max-w-6xl mx-auto space-y-6">
        <h1 className="text-2xl font-bold text-primary mb-4">Platform Settings</h1>

        {/* General Information */}
        <section className="bg-surface border-border shadow-md rounded-xl p-6">
          <h2 className="text-xl font-semibold text-textPrimary mb-4">University Information</h2>
          <div className="grid sm:grid-cols-2 gap-4">
            <div>
              <Label htmlFor="universityName" className="block text-sm text-textSecondary mb-1">
                University Name
              </Label>
              <Input
                id="universityName"
                type="text"
                value={settings.universityName}
                onChange={(e) => handleChange("universityName", e.target.value)}
              />
            </div>
            <div>
              <Label htmlFor="currentSession" className="block text-sm text-textSecondary mb-1">
                Current Session
              </Label>
              <Input
                id="currentSession"
                type="text"
                value={settings.currentSession}
                onChange={(e) => handleChange("currentSession", e.target.value)}
              />
            </div>
            <div>
              <Label htmlFor="currentSemester" className="block text-sm text-textSecondary mb-1">
                Current Semester
              </Label>
              <Select
                value={settings.currentSemester}
                onValueChange={(value) => handleChange("currentSemester", value)}
              >
                <SelectTrigger>
                  <SelectValue placeholder="Select semester" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="First Semester">First Semester</SelectItem>
                  <SelectItem value="Second Semester">Second Semester</SelectItem>
                </SelectContent>
              </Select>
            </div>
            <div>
              <Label htmlFor="maxCreditUnitsPerSemester" className="block text-sm text-textSecondary mb-1">
                Max Credit Units Per Semester
              </Label>
              <Input
                id="maxCreditUnitsPerSemester"
                type="number"
                value={settings.maxCreditUnitsPerSemester}
                onChange={(e) => handleChange("maxCreditUnitsPerSemester", Number(e.target.value))}
              />
            </div>
          </div>
        </section>

        {/* Grading System */}
        <section className="bg-surface border-border shadow-md rounded-xl p-6">
          <h2 className="text-xl font-semibold text-textPrimary mb-4">Grading System</h2>
          <div className="grid grid-cols-3 sm:grid-cols-6 gap-4">
            {Object.entries(settings.gradingSystem).map(([grade, value]) => (
              <div key={grade} className="text-center">
                <p className="font-semibold text-textPrimary">{grade}</p>
                <Input
                  type="number"
                  value={value}
                  onChange={(e) =>
                    handleChange("gradingSystem", {
                      ...settings.gradingSystem,
                      [grade]: Number(e.target.value),
                    })
                  }
                  className="mt-1"
                />
              </div>
            ))}
          </div>

          <div className="mt-4">
            <Label htmlFor="cgpaScale" className="block text-sm text-textSecondary mb-1">
              CGPA Scale
            </Label>
            <Input
              id="cgpaScale"
              type="number"
              step="0.1"
              value={settings.cgpaScale}
              onChange={(e) => handleChange("cgpaScale", Number(e.target.value))}
            />
          </div>
        </section>

        {/* Toggles */}
        <section className="bg-surface border-border shadow-md rounded-xl p-6">
          <h2 className="text-xl font-semibold text-textPrimary mb-4">Active Controls</h2>
          <div className="grid sm:grid-cols-2 gap-6">
            {/* Registration */}
            <div className="flex justify-between items-center">
              <span className="text-textPrimary font-medium">Course Registration</span>
              <Switch
                checked={settings.registrationOpen}
                onCheckedChange={() => handleToggle("registrationOpen")}
              />
            </div>

            {/* Results */}
            <div className="flex justify-between items-center">
              <span className="text-textPrimary font-medium">Result Publication</span>
              <Switch
                checked={settings.resultPublicationOpen}
                onCheckedChange={() => handleToggle("resultPublicationOpen")}
              />
            </div>
          </div>
        </section>

        {/* Save Button */}
        <div className="flex justify-end">
          <Button onClick={saveSettings} disabled={loading}>
            {loading ? "Saving..." : "Save Changes"}
          </Button>
        </div>
      </div>
    </main>
  );
}